@import url(../../../Fonts/Workbench/Workbench.css);

.hero-container {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1;
  grid-column-gap: 10px;
  grid-row-gap: 0px;
  padding: 2%;
}
.workbench-style {
  font-family: "workbench", sans-serif;
  font-weight: 100 !important;
}
.welcome-container-left {
  grid-area: 1 / 1 / 2 / 2;
}
.welcome-container-right {
  grid-area: 1 / 2 / 2 / 3;
  justify-self: center;
}
.go-to-projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  left: 45%;
  border-radius: 50%;
  background: #2f0e58;
  width: 100px;
  height: 100px;
  cursor: pointer;
  border: none;
  outline: none;
  transition: all 0.2s ease-in-out;
}

.go-to-projects:hover {
  transition: all 0.2s ease-in-out;
  animation: buttonfade 1s infinite;
  background: rgb(61 23 107 / 72%);
}
.go-to-projects:active {
  transform: translate(0px, 3px);
  background: rgba(66, 36, 102, 0.476);
}

.image-container {
  justify-self: center;
  border-radius: 50%;
}
.propic-image {
  background: radial-gradient(
    circle at center,
    rgba(136, 0, 255, 0.1) 0%,
    rgba(136, 0, 255, 0.2) 100%,
    transparent
  );
  width: 50vh;
  border-radius: 50%;
  padding: 10px;
}
.cta{
  text-align: center;
  padding: 6%;
}

@keyframes buttonfade {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

@media screen and (max-width: 768px) {
  .hero-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-items: center;
  }
  
  .welcome-container-right { grid-area: 1 / 1 / 2 / 2; }
  .welcome-container-left { grid-area: 2 / 1 / 3 / 2; }
  .go-to-projects { grid-area: 3 / 1 / 4 / 2;left: 0; }
  .contact-me { grid-area: 4 / 1 / 5 / 2; }
}
