.tooltip {
  position: fixed;
  background-color: #ffffff;
  color: #220040;
  padding: 5px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(255, 255, 255, 0.1);
  z-index: 999;
  transition: all 1s ease-in-out;
}

sup {
  cursor: pointer;
}
.projects-images {
  grid-area: 1 / 1 / 2 / 2;
}
.project-image {
  width: 45vw;
  margin: 10px;
  border-radius: 10px;
}
.projects-container {
  grid-area: 1 / 2 / 2 / 3;
}
.h2-description,
.h2-projects {
  text-align: center;
  margin-top: 6%;
}
.projects-container > h3 {
  text-align: center;
  margin-top: 4%;
}
.projects {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 15px;
  grid-row-gap: 0px;
  padding: 2%;
}
.demolink {
  text-decoration: none;
  color: rgba(176, 111, 255, 0.721);
}
.jumper-svg {
  animation: svgjump 1s infinite;
  -webkit-animation: svgjump 1s infinite;
}
.navigator-element {
  padding: 1%;
  margin: 5% 1%;
  background: rgb(0 0 0 / 38%);
  border-radius: 8px;
  box-shadow: 0 4px 30px rgb(72 0 133 / 15%);
  transition: 0.3s;
}
.navigator-element:hover {
  box-shadow: 0 0 10px 1px rgba(149, 27, 255, 0.7); /* Effetto glow */
}
.navigator-element h3 {
  text-align: center;
}

.carousel-arrow {
  position: absolute;
  z-index: 9999999999;
  color: rgb(220, 220, 220);
  cursor: pointer;
  transition: 0.2s ease-in;
}
.carousel-arrow:hover {
  color: rgb(161, 161, 161);
  transition: 0.2s ease-in;
}
.carousel-arrow:active {
  color: rgb(100, 100, 100);
  transition: 0.1s ease-in;
}

.left-arrow {
  left: 2%;
}
.right-arrow {
  right: 2%;
}
.fade-in {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.fade-out {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

@keyframes svgjump {
  0% {
    transform: translateY(
      0
    ); /* Inizio dell'animazione: nessuna trasformazione */
  }
  50% {
    transform: translateY(
      -10px
    ); /* Met√† dell'animazione: spostamento verso l'alto */
  }
  100% {
    transform: translateY(
      0
    ); /* Fine dell'animazione: torna alla posizione originale */
  }
}
